.formLabel {
  display: flex;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  color: var(--secondary-text-color);
  &.formLabelFocused {
    color: var(--secondary-text-color);
  }

  &.disabled {
    color: var(--secondary-text-color);
    opacity: 0.3;
  }

  &.formLabelError {
    color: var(--error-text-color);
  }
}
