.dropdownMenu {
  height: 32px;
  padding: 0 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: var(--primary-text-color);
  background-color: transparent;
  border-color: var(--primary-stroke-color);
  fill: var(--primary-text-color); /* for svg icons */

  &.sizeExtraSmall {
    height: 24px;
  }

  &:active,
  &:focus-visible,
  &:hover,
  &.open {
    border-color: var(--primary-text-color);
  }

  &:disabled {
    color: var(--primary-text-color);
    background-color: transparent;
    border-color: transparent;
    opacity: 0.3;
    fill: var(--primary-text-color);
  }
}

.dropdownIcon {
  height: 20px;
}

.labelMargin {
  margin-left: 6px;
}

.dropdownArrow {
  margin-top: 2px;
  margin-left: 12px;

  &.open {
    transform: rotateZ(180deg);
  }
}

.verticalLine {
  width: 1px;
  height: 22px;
  margin-right: 12px;
  margin-left: 8px;
  border-left: 1px solid var(--primary-stroke-color);
}

.multiSelectWrapper {
  display: flex;
  align-items: center;
}

.disabled {
  opacity: 0.3;
}
