$button-padding-horizontal: 24px;
$button-padding-vertical-extrasmall: 4px;
$button-padding-vertical-small: 8px;
$button-padding-vertical-medium: 12px;
$button-padding-vertical-large: 16px;

@function outline-padding($padding) {
  @return $padding - 1px;
}

.root {
  padding: $button-padding-vertical-medium $button-padding-horizontal;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;

  &:focus-visible {
    outline-width: 2px;
    outline-style: solid;
    outline-offset: 1px;
  }
}

.sizeLarge {
  padding-top: $button-padding-vertical-large;
  padding-bottom: $button-padding-vertical-large;
  font-size: 18px;
  line-height: 24px;
}

.sizeSmall {
  padding-top: $button-padding-vertical-small;
  padding-bottom: $button-padding-vertical-small;
}

.sizeExtraSmall {
  padding-top: $button-padding-vertical-extrasmall;
  padding-bottom: $button-padding-vertical-extrasmall;
  font-size: 12px;
  .startIcon,
  .endIcon {
    height: 14px;
  }

  &.outlined {
    padding-top: outline-padding($button-padding-vertical-extrasmall);
    padding-bottom: outline-padding($button-padding-vertical-extrasmall);
  }
}

.outlined {
  padding: outline-padding($button-padding-vertical-medium)
    outline-padding($button-padding-horizontal);
}

.outlinedSizeLarge {
  padding-top: outline-padding($button-padding-vertical-large);
  padding-bottom: outline-padding($button-padding-vertical-large);
}

.outlinedSizeSmall {
  padding-top: outline-padding($button-padding-vertical-small);
  padding-bottom: outline-padding($button-padding-vertical-small);
}

.iconSizeSmall,
.iconSizeMedium {
  height: 16px;
}

.iconSizeLarge {
  height: 24px;
}

.primary {
  color: var(--white-text-color);
  background-color: var(--primary-accent-color);
  fill: var(--white-text-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--primary-accent-color);
  }

  &:hover {
    background-color: var(--primary-hover-color);
  }

  &:active {
    background-color: var(--primary-onclick-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    background-color: var(--disabled-state-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}

.secondary {
  color: var(--primary-accent-color);
  background-color: transparent;
  border-color: var(--primary-accent-color);
  fill: var(--primary-accent-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--primary-accent-color);
  }

  &:hover {
    color: var(--white-text-color);
    background-color: var(--primary-hover-color);
    fill: var(--white-text-color); /* for svg icons */
  }

  &:active {
    color: var(--white-text-color);
    background-color: var(--primary-onclick-color);
    fill: var(--white-text-color); /* for svg icons */
  }

  &.disabled {
    color: var(--disabled-text-color);
    border-color: var(--disabled-state-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}

.tertiary {
  color: var(--primary-text-color);
  background-color: transparent;
  border-color: var(--primary-stroke-color);
  fill: var(--primary-text-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--primary-stroke-color);
  }

  &:hover {
    background-color: var(--hover-state-color);
  }

  &:active {
    background-color: var(--onclick-state-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    border-color: var(--disabled-state-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}

.ghost {
  color: var(--primary-text-color);
  background-color: transparent;
  fill: var(--primary-text-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--primary-stroke-color);
    outline-offset: -2px;
  }

  &:hover {
    background-color: var(--hover-state-color);
  }

  &:active {
    background-color: var(--onclick-state-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}

.destructive {
  color: var(--white-text-color);
  background-color: var(--error-color);
  fill: var(--white-text-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--error-stroke-color);
  }

  &:hover {
    background-color: var(--error-hover-color);
  }

  &:active {
    background-color: var(--error-onclick-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    background-color: var(--disabled-state-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}

.confirmative {
  color: var(--white-text-color);
  background-color: var(--success-color);
  fill: var(--white-text-color); /* for svg icons */

  &:focus-visible {
    outline-color: var(--success-stroke-color);
  }

  &:hover {
    background-color: var(--success-hover-color);
  }

  &:active {
    background-color: var(--success-onclick-color);
  }

  &.disabled {
    color: var(--disabled-text-color);
    background-color: var(--disabled-state-color);
    fill: var(--disabled-text-color); /* for svg icons */
  }
}
