.root {
  background-color: var(--tertiary-background-color);

  .label {
    font-size: 12px;
    line-height: 16px;
    color: var(--primary-text-color);
  }

  .iconSmall {
    width: unset;
    height: 10px;
    margin-right: -2px;
    margin-left: 10px;
    fill: var(--primary-text-color);
  }

  &.disabled {
    opacity: 0.3;
  }
}

.closeButton {
  min-width: 0;
  max-width: fit-content;
  padding: 3px;
  margin-right: 6px;
  border: unset !important; // stylelint-disable-line declaration-no-important
  border-radius: 6px;
  stroke-width: 75;
  stroke: var(--primary-text-color);
  svg {
    height: 6px;
  }

  &:hover {
    background-color: var(--primary-hover-color);
    stroke: var(--inverse-text-color);
  }

  &:active,
  &:focus-visible {
    background-color: var(--primary-onclick-color);
    stroke: var(--inverse-text-color);
  }
}
