.menuPaper {
  max-height: 320px;
  background-color: var(--primary-background-color);
}

.menuList {
  padding: 0;
}

.menuItem {
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 14px;
  line-height: 16px;

  &.highlighted {
    color: var(--accent-primary-text-color);
    .optionIcon {
      fill: var(--accent-primary-text-color);
    }
  }

  &:hover,
  &.hover {
    background-color: var(--secondary-background-color);
  }

  &.selected {
    color: var(--inverse-text-color);
    background-color: var(--primary-accent-color);
    .optionIcon,
    .selectedIcon {
      fill: var(--inverse-text-color);
    }
  }

  &:global(.Mui-disabled) {
    opacity: 0.3;
  }
}

.optionIcon {
  margin-right: 6px;
}

.selectedSpacer {
  margin-right: 32px;
}

.selectedIcon {
  position: absolute;
  right: 16px;
}

.menuWrapper {
  display: inline-block;
}
