.label {
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  color: var(--secondary-text-color);

  &.disabled {
    color: var(--secondary-text-color);
    opacity: 0.3;
  }

  &:global(.MuiFormLabel-filled) {
    color: var(--primary-text-color);
  }

  &.error:not(.disabled):not(.focused) {
    color: var(--error-text-color);
  }

  &.focused,
  &.focused .asterisk {
    color: var(--accent-primary-text-color);
  }

  &.shrink {
    transform: translate(14px, -7px) scale(1);
  }
}

.inputRoot {
  .input {
    padding: 11.7px 16px;
    font-size: 14px;
    line-height: 16px;
    color: var(--primary-text-color);
  }

  &.alignRight .input {
    text-align: right;
  }

  .inputAdornedStart {
    padding-left: 0;
  }

  .inputAdornedEnd {
    padding-right: 0;
  }

  &.error {
    .notchedOutline {
      border-color: var(--error-color);
    }
  }

  &.disabled {
    opacity: 0.3;
    .notchedOutline {
      border-color: var(--secondary-stroke-color);
    }
  }

  .notchedOutline {
    border-color: var(--secondary-stroke-color);
  }

  &.focused {
    .notchedOutline {
      border-color: var(--primary-accent-color);
      border-width: 1px;
    }
  }

  legend {
    font-size: 14px;
  }
}

.adornment {
  font-size: 14px;
  line-height: 16px;

  > :not(:last-child) {
    margin-right: 10px;
  }

  .errorAdornment {
    fill: var(--error-color);
  }

  &.disabled .errorAdornment {
    fill: var(--primary-text-color);
  }

  .infoTooltip {
    line-height: 0;
  }

  .prefixSufix {
    color: var(--tertiary-text-color);
    fill: var(--tertiary-text-color);
  }
}

.helperWithLength {
  display: flex;
  justify-content: space-between;
  > :not(:first-child) {
    margin-left: 32px;
  }
}
