.iconButton {
  min-width: 0;
  max-width: fit-content;
  border-radius: 4px;
  svg {
    height: 16px;
  }
}

.extraSmall {
  svg {
    height: 10px;
  }
  padding: 7px;
}

.small {
  padding: 8px;
}

.medium {
  padding: 12px;
}

.large {
  padding: 20px;
}

.round {
  &.extraSmall {
    border-radius: 14px;
  }

  &.small {
    border-radius: 16px;
  }

  &.medium {
    border-radius: 20px;
  }

  &.large {
    border-radius: 28px;
  }
}

.noBorder {
  border: unset !important; // stylelint-disable-line declaration-no-important
}
