.switchBase {
  .thumb {
    background-color: var(--tertiary-background-color);
  }

  + .track {
    background-color: var(--quaternary-background-color);
    opacity: 1;
  }

  &.checked {
    .thumb {
      background-color: var(--success-color);
    }

    + .track {
      background-color: var(--success-hover-color);
      opacity: 1;
    }
  }

  // below we are picking the color instead of setting 'opacity: 0.3'
  // due to the superposition of the thumb and track elements
  &.disabled {
    .thumb {
      background-color: var(--secondary-background-color);
    }

    + .track {
      opacity: 0.3;
    }

    &.checked {
      .thumb {
        background-color: var(--success-onclick-color);
      }

      + .track {
        opacity: 0.3;
      }
    }
  }
}
