:global(.theme-shades) {
  /* Primary Colors */
  --shade-violet-10: #dbc3fb;
  --shade-violet-20: #c19df9;
  --shade-violet-30: #aa77f6;
  --shade-violet-40: #9653e4;
  --shade-violet-50: #9141e6;
  --shade-violet-60: #821fdf;
  --shade-violet-70: #6e0ccb;
  --shade-violet-80: #519;
  --shade-violet-90: #380c66;
  --shade-violet-100: #2a094d;

  /* Secondary Colors */
  --shade-magenta-10: #fbe4ed;
  --shade-magenta-20: #f5bad3;
  --shade-magenta-30: #f08eb6;
  --shade-magenta-40: #ec6098;
  --shade-magenta-50: #e83c81;
  --shade-magenta-60: #e6126a;
  --shade-magenta-70: #d51167;
  --shade-magenta-80: #bf1061;
  --shade-magenta-90: #a90e5c;
  --shade-magenta-100: #840b53;

  /* Background, Text and Element Shades */
  --shade-blue-gray-10: #f0f2f4;
  --shade-blue-gray-20: #d3d8de;
  --shade-blue-gray-30: #b6bdc8;
  --shade-blue-gray-40: #99a3b2;
  --shade-blue-gray-50: #7c899d;
  --shade-blue-gray-60: #626f83;
  --shade-blue-gray-70: #4d5766;
  --shade-blue-gray-80: #373e49;
  --shade-blue-gray-90: #21252c;
  --shade-blue-gray-100: #121216;

  /* Error Shades */
  --shade-red-10: #ffeaed;
  --shade-red-20: #ffcad0;
  --shade-red-30: #f69595;
  --shade-red-40: #ed6a6b;
  --shade-red-50: #f84445;
  --shade-red-60: #fd2c27;
  --shade-red-70: #ee1d27;
  --shade-red-80: #dc0922;
  --shade-red-90: #cf001a;
  --shade-red-100: #c1000a;

  /* Success Shades */
  --shade-green-10: #c3e7cb;
  --shade-green-20: #9cd8aa;
  --shade-green-30: #72ca88;
  --shade-green-40: #50be6f;
  --shade-green-50: #28b256;
  --shade-green-60: #1fa34c;
  --shade-green-70: #119140;
  --shade-green-80: #008035;
  --shade-green-90: #006121;
  --shade-green-100: #00471e;

  /* Info Shades */
  --shade-blue-10: #def0fe;
  --shade-blue-20: #b0dafd;
  --shade-blue-30: #7fc4fc;
  --shade-blue-40: #4dacfa;
  --shade-blue-50: #249bf9;
  --shade-blue-60: #008bf9;
  --shade-blue-70: #007ceb;
  --shade-blue-80: #006ddb;
  --shade-blue-90: #0058c2;
  --shade-blue-100: #0b3ca1;

  /* Warning Shades */
  --shade-yellow-10: #fff7df;
  --shade-yellow-20: #ffebb1;
  --shade-yellow-30: #ffdd84;
  --shade-yellow-40: #ffd25b;
  --shade-yellow-50: #ffc644;
  --shade-yellow-60: #ffbd3a;
  --shade-yellow-70: #ffaf36;
  --shade-yellow-80: #ff9b33;
  --shade-yellow-90: #ff8930;
  --shade-yellow-100: #ff6c2b;

  /* Gunmetal Shades */
  --shade-gunmetal-light: #dadfe2;
  --shade-gunmetal-dark: #555b6e;

  /* Misc UI Colors */
  --white-ui-color: #fff;
  --black-ui-color: #000;
  --coral-ui-color: #eb1605;
  --teal-ui-color: #0f8573;
  --steel-blue-ui-color: #4279ad;
  --orchid-ui-color: #974bda;
}

:global(.theme-light) {
  /* Primary Colors */
  --primary-accent-color: var(--shade-violet-80);
  --primary-hover-color: var(--shade-violet-90);
  --primary-selected-color: var(--shade-violet-90);
  --primary-onclick-color: var(--shade-violet-100);

  /* Secondary Colors */
  --secondary-accent-color: var(--shade-magenta-60);
  --secondary-hover-color: var(--shade-magenta-70);
  --secondary-selected-color: var(--shade-magenta-70);
  --secondary-onclick-color: var(--shade-magenta-80);

  /* Background Colors */
  --primary-background-color: var(--white-ui-color);
  --secondary-background-color: var(--shade-blue-gray-10);
  --tertiary-background-color: var(--shade-blue-gray-20);
  --quaternary-background-color: var(--shade-blue-gray-30);
  --high-emphasis-background-color: var(--shade-blue-gray-90);

  /* Stroke Colors */
  --primary-stroke-color: var(--shade-blue-gray-70);
  --secondary-stroke-color: var(--shade-blue-gray-50);
  --tertiary-stroke-color: var(--shade-blue-gray-20);
  --error-stroke-color: var(--shade-red-80);
  --success-stroke-color: var(--shade-green-80);
  --info-stroke-color: var(--shade-blue-80);
  --warning-stroke-color: var(--shade-yellow-60);

  /* Text and Elements Colors */
  --primary-text-color: var(--shade-blue-gray-100);
  --secondary-text-color: var(--shade-blue-gray-80);
  --tertiary-text-color: var(--shade-blue-gray-60);
  --inverse-text-color: var(--white-ui-color);
  --white-text-color: var(--white-ui-color);
  --black-text-color: var(--black-ui-color);
  --accent-primary-text-color: var(--shade-violet-80);
  --accent-primary-hover-color: var(--shade-violet-90);
  --accent-secondary-text-color: var(--shade-magenta-60);
  --accent-secondary-hover-color: var(--shade-magenta-70);
  --error-text-color: var(--shade-red-80);
  --success-text-color: var(--shade-green-80);
  --info-text-color: var(--shade-blue-80);
  --warning-text-color: var(--shade-yellow-70);
  --disabled-01-text-color: var(--shade-blue-gray-40);
  --disabled-02-text-color: var(--shade-blue-gray-50);
  --disabled-03-text-color: var(--shade-blue-gray-60);
  --disabled-04-text-color: var(--shade-blue-gray-70);

  /* Semantic Colors */
  --error-color: var(--shade-red-80);
  --error-hover-color: var(--shade-red-90);
  --error-onclick-color: var(--shade-red-100);
  --success-color: var(--shade-green-80);
  --success-hover-color: var(--shade-green-90);
  --success-onclick-color: var(--shade-green-100);
  --info-color: var(--shade-blue-80);
  --info-hover-color: var(--shade-blue-90);
  --warning-color: var(--shade-yellow-70);
  --warning-hover-color: var(--shade-yellow-80);

  /* UI Colors - other misc UI colors are defined in shades class */
  --gunmetal-ui-color: var(--shade-gunmetal-dark);

  /* State Colors */
  --disabled-01-state-color: var(--shade-blue-gray-20);
  --disabled-02-state-color: var(--shade-blue-gray-30);
  --disabled-03-state-color: var(--shade-blue-gray-40);
  --disabled-04-state-color: var(--shade-blue-gray-50);
  --hover-01-state-color: var(--shade-blue-gray-10);
  --hover-02-state-color: var(--shade-blue-gray-20);
  --hover-03-state-color: var(--shade-blue-gray-30);
  --hover-04-state-color: var(--shade-blue-gray-40);
  --selected-01-state-color: var(--shade-blue-gray-10);
  --selected-02-state-color: var(--shade-blue-gray-20);
  --selected-03-state-color: var(--shade-blue-gray-30);
  --selected-04-state-color: var(--shade-blue-gray-40);
  --onclick-01-state-color: var(--shade-blue-gray-20);
  --onclick-02-state-color: var(--shade-blue-gray-30);
  --onclick-03-state-color: var(--shade-blue-gray-40);
  --onclick-04-state-color: var(--shade-blue-gray-50);
}

