.pagination {
  display: flex;
  align-items: center;
  > :not(:last-child) {
    margin-right: 8px;
  }
}

.paginationButton {
  padding: 12px;
  color: var(--tertiary-text-color);
  fill: var(--tertiary-text-color);
  svg {
    width: 8px;
    height: 8px;
  }

  &.fixedSize {
    width: 0;
    height: 0;
    padding: 16px;
  }

  &.selected {
    color: var(--primary-text-color);
    background-color: var(--tertiary-background-color);
    opacity: 1;
  }
}
