.select {
  width: 100%;

  :global(.MuiOutlinedInput-adornedEnd),
  :not(:global(.MuiOutlinedInput-adornedEnd)) :global(.MuiSelect-root) {
    padding-right: 32px;
  }

  :global(.MuiSelect-icon) {
    color: var(--secondary-stroke-color);
  }
  :global(.MuiFormLabel-filled)
    + :global(.MuiInputBase-root)
    :global(.MuiSelect-icon) {
    color: var(--primary-text-color);
  }
}

.menuPaper {
  background-color: var(--primary-background-color);
}

.menuList {
  padding: 0;
}
