$input-z-index: 2;

.root {
  padding: 0;
  color: var(--primary-stroke-color);
  &:global(.Mui-disabled) {
    color: var(--primary-stroke-color);
  }

  &:hover,
  &:global(.Mui-focusVisible) {
    background-color: transparent;

    &:not(:global(.Mui-checked)):not(:global(.Mui-indeterminate)) {
      &::after {
        position: absolute;
        z-index: calc(#{$input-z-index} - 2);
        width: 16px;
        content: url("./HoverBackground.svg");
      }
      &.dark::after {
        content: url("./HoverBackgroundDark.svg");
      }
    }
  }

  :global(.MuiSvgIcon-root) {
    z-index: calc(#{$input-z-index} - 1);
    margin: -2px;
  }

  input {
    z-index: $input-z-index;
  }
}

.checked,
.checked.dark {
  color: var(--primary-accent-color);
  &:global(.Mui-disabled) {
    color: var(--primary-accent-color);
  }
}

.disabled {
  opacity: 0.3;
}
