.tableContainer {
  font-size: 14px;
  line-height: 16px;
}

.tableNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 32px; // avoid pagination appearing and resizing the table
}

.tableDetail {
  display: flex;
  align-items: center;
  > :not(:first-child) {
    margin-left: 16px;
  }
}

.tableBodyRoot {
  background-color: var(--primary-background-color);
}

.tableRowRoot {
  &.selectable {
    cursor: pointer;
  }

  &:hover {
    background-color: var(--secondary-background-color);
  }

  &.selected {
    background-color: var(--tertiary-background-color);
  }
}

.tableCellRoot {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 0 12px;
  border-color: var(--secondary-background-color);
}

.tableCellHead {
  display: flex;
  align-items: center;
  height: 40px;
  padding: 0 12px;
  font-weight: 600;
  line-height: 16px;
}

.headCellTitle {
  display: flex;
  align-items: center;
  color: var(--tertiary-text-color);
  text-align: start;
  border-bottom: none;
  svg {
    fill: var(--tertiary-text-color);
  }

  &:hover,
  &.sorted {
    color: var(--primary-text-color);
    svg {
      fill: var(--primary-text-color);
    }
  }

  &:hover .sortArrow {
    display: inline-block;
  }
}

.sortArrow {
  margin-left: 16px;
}

.sortArrowHidden {
  display: none;
}

.resizer {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  display: inline-block;
  width: 10px;
  height: 100%;
  touch-action: none;
  transform: translateX(50%);
  &.isResizing {
    // resizer color if needed
  }
}

.tableWrap {
  display: block;
  max-width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
}
